    }
    .skill-item-box{
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 200px;
        width: 1100px;
    }
    .skill-item-box li{
        float: left;
        width: 230px;
        margin: 0 10px;
        text-align: center;
        position: relative;
    }
    .skill-items>li::after{
        content:'';
